<template lang="html">
  <div class="container iniciators__container" id="iniciatorsS">
    <Title lefText="To the initiators"/>
    <div class="card__features">
      <div class="fix__block">

      </div>
      <h3 class="ml-3 mb-2" >Benefits</h3>
      <ul class="ml-3 mt-2" style="margin-top: 60px;">
        <li> Create your own project tokens and conduct TGE </li>
        <li> Individual investment search for presale, contact details available </li>
        <li> The possibility of obtaining a loan or grant in an environmental investment fund </li>
        <li> Create a cryptograph </li>
        <li> Obtaining an objective rating of the project and advisors </li>
        <li> PR promotion of projects in the media </li>
        <li> Volunteer opportunities </li>

      </ul>
      <h3 class="ml-3 mb-2 mt-5 col-md-6">
        List of required documents required for project placement
      </h3>
      <div class="row">
        <div class="col-lg-5">
          <ul class="docs__list" style="list-style-type:none;">
            <li><button v-on:click="whitePaper" class="btn btn-outline-success">1. Whitepaper</button></li>
            <li><button v-on:click="Onepager" class="btn btn-outline-success">2. Onepager</button></li>
            <li><button v-on:click="Pitch" class="btn btn-outline-success">3. Pitch deck</button></li>
            <li><button v-on:click="Business" class="btn btn-outline-success">4. Business plan</button></li>
          </ul>
        </div>
        <div class="col-lg-5">
          <div v-if="isWitePaper" class="">
            <a href="#"><img src="../../assets/wp.jpg" class="mw-100" alt="White paper"></a>
          </div>
          <div v-if="isOnePager" class="">
          <a href="#">  <img src="../../assets/op.jpg" class="mw-100" alt=""></a>
          </div>
          <div v-if="isPitchDeck" class="">
            <a href="#"><img src="../../assets/pd.jpg" class="mw-100" alt=""></a>
          </div>
          <div v-if="isBusinessPlan" class="">
            <a href="#"><img src="../../assets/bp.jpg" class="mw-100" alt=""></a>
          </div>
        </div>
      </div>
      <div class="row pl-5 ml-1">
        <button
          v-on:click="setIniciators"
          v-bind:disabled="!isSignUp"
          type="button"
          class="btn btn-outline-indigo"
          name="button"
          id="btn">
          Add project
        </button>
      </div>
      <div class="row mb-5 pl-5 ml-1">
        <span class="red-text" v-if="!isSignUp">You can add your project only after registering on the site</span>

      </div>
      <h3 class="ml-3 mb-2 mt-5">Investor search for presale</h3>
      <p class="lead ml-3">Use the main and additional services of the Business Platform resource: </p>
            <ul>
              <li>Preparation of the necessary documents </li>
              <li>Search for advisors, </li>
              <li>Express project analysis and expertise </li>
              <li>Priority placement </li>
              <li>E-mailing on the basis of investors </li>
            </ul>
    </div>
  </div>
</template>

<script>
import Title from './Title'
export default {
  name: 'Iniciators',
  components: {
    Title
  },
  data () {
    return {
      isWitePaper: false,
      isOnePager: false,
      isPitchDeck: false,
      isBusinessPlan: false,
      isSignUp: false,
    }
  },
  mounted () {
    if (localStorage.getItem('user') === 'false') {
      console.log(localStorage.getItem('user') + ' !== false')
      this.isSignUp = false;
    } else if (localStorage.getItem('user') == null){
      console.log(localStorage.getItem('user') + ' !== null')

    } else if (localStorage.getItem('user') === 'true'){
      console.log(localStorage.getItem('user'))
      this.isSignUp = true;



    }
  },
  methods: {
    setIniciators (){
        this.$router.push({ name: 'SetProjectEn'})
    },
    whitePaper () {
      this.isWitePaper = !this.isWitePaper;
      this.isOnePager = false;
      this.isPitchDeck = false;
      this.isBusinessPlan = false;

    },
    Onepager () {
      this.isOnePager = !this.isOnePager;
      this.isWitePaper = false;
      this.isPitchDeck = false;
      this.isBusinessPlan = false;


    },
    Pitch () {
      this.isPitchDeck = !this.isPitchDeck;
      this.isBusinessPlan = false;
      this.isOnePager = false;
      this.isWitePaper = false;
    },
    Business () {
      this.isBusinessPlan = !this.isBusinessPlan;
      this.isPitchDeck = false;
      this.isOnePager = false;
      this.isWitePaper = false;
    }

  },

}
</script>

<style lang="css">
.iniciators__container{
  padding-top: 60px;
}
.card__features{
  width: 100%;
  margin: 0 auto;
}

.fix__block {
height: 52px;
}
.docs__list {
  margin-top: 20px;
}
</style>



