<template lang="html">
  <div class="container-fluid">
    <h1 class="text-center">
      {{txt}}
    </h1>
    <h1 class="text-left">
      {{txtLeft}}
    </h1>
    <h2 class="smtext text-center mt-5">{{smalltxt}}</h2>
    <h2 class="text-left mt-5">{{lefText}}</h2>
  </div>
</template>

<script>

export default {
  name: 'Title',

  props: {
    txt: String,
    smalltxt: String,
    lefText: String,
    txtLeft: String,
  }
}
</script>

<style lang="css">
.container-fluid h1{
  width: 60%;
  margin: 0 auto;
  font-size: 40px;
  font-weight: 600;
}
.container-fluid .smtext {
  display: block;
  width: 60%;
  margin: 0 auto;
}
</style>



