<template lang="html">
  <div class="container-fluid --container-link">
    <ul class="row list-link">
      <div class="navbar-brand ml-2">
        <img src="../../assets/logo-white.png" alt="">
      </div>
      <li class="link-item lead mr-3 ml-3"><a class="linkScroll" href="#projectsS">Проекты</a></li>
      <li class="link-item lead mr-3 ml-3"><a class="linkScroll" href="#investFondSingle">Ивестиционный фонд</a></li>
      <li class="link-item lead mr-3 ml-3"><a class="linkScroll" href="#investorsS">Инвесторы</a></li>
      <li class="link-item lead mr-3 ml-3"><a class="linkScroll" href="#volonteersSingle">Волонтеры</a></li>
      <li class="link-item lead mr-3 ml-3"><a class="linkScroll" href="#">Новости</a></li>
      <btn color="outline-indigo" class="ml-5" @click.native="modal = true">{{isLoginUser}}</btn>
        <button v-on:click="change" class="btn btn-outline-indigo">EN</button>


    </ul>
    <modal v-if="modal" @close="modal = false">
      <modal-header>
        <modal-title>
          <div class="card-img ">
            <img src="../../assets/logo.png" charset="utf-8"></img>
          </div>
        </modal-title>
      </modal-header>
      <modal-body>
        <!-- Default form login -->
        <form @submit.prevent.default v-if="!isLogin">
          <div class="row pl-3 pr-3" >
            <label
            class="ml-3 mr-3"
              v-bind:class=" isRed ? 'red-text' : ''"
             for="email">{{answer}}</label>
               <label
                 v-bind:class=" isRedPswd ? 'red-text' : ''"
                for="password">{{answerPassword}}</label>

          </div>
          <input
            v-model="isEmail"
            class="form-control"
            style="display:inline;width:auto;"
            v-bind:class=" isRed ? 'red__outline' : ''"
            type="text" id="email"
            name="email"
            placeholder="Email"/>

          <input
            class="form-control"
            style="display:inline;width:auto;"
            type="password" id="password"
            v-model="isPassword"
            v-bind:class=" isRedPswd ? 'red__outline' : ''"
            name="password"
            placeholder="Password"/>

          <button
          v-if="!SignIn"
            class="btn btn-outline-success"
            id="quickstart-sign-in"
            name="signin"
            v-on:click="toggleSignIn"
            >
            Войти
          </button>
          <button
          v-if="SignIn"
            class="btn btn-outline-success"
            id="quickstart-sign-in"
            name="signin"
            v-on:click="signOut"
            >
            Выйти
          </button>
          <div
            class="quickstart-user-details-container">
             {{ status}}
            <span id="quickstart-sign-in-status">{{isUser}}</span>
          </div>
        </form>
        <!-- Default form login -->
              <!-- Default form register -->
        <form v-if="isLogin">
          <div class="row pl-3 pr-3" >
            <label
            class="ml-3 mr-3"
              v-bind:class=" isRed ? 'red-text' : ''"
             for="email">{{answer}}</label>
               <label
                 v-bind:class=" isRedPswd ? 'red-text' : ''"
                for="password">{{answerName}}</label>
                <label
                  v-bind:class=" isRedPswd ? 'red-text' : ''"
                 for="password">{{answerFamili}}</label>
                 <label
                   v-bind:class=" isRedPswd ? 'red-text' : ''"
                  for="password">{{answerDoble}}</label>
                  <label
                    v-bind:class=" isRedPswd ? 'red-text' : ''"
                   for="password">{{answerReg}}
                   <button v-if="!isSinU" class="btn btn-outline-indigo" color="outline-indigo" v-on:click="logined">Войти</button>

                 </label>
                   <label
                     v-bind:class=" isRedPswd ? 'red-text' : ''"
                    for="password">{{answerAlredy}}</label>

          </div>
          <input
            v-model="isEmail"
            class="form-control mb-2"
            style="display:inline;width:auto;"
            v-bind:class=" isRed ? 'red__outline' : ''"
            type="text" id="email"
            name="email"
            placeholder="Email"/>

          <input
            class="form-control mb-2"
            style="display:inline;width:auto;"
            type="text" id="name"
            v-model="isName"
            v-bind:class=" isRedPswd ? 'red__outline' : ''"
            name="name"
            placeholder="Ваше имя"/>
            <input
              class="form-control mb-2"
              style="display:inline;width:auto;"
              type="text" id="famili"
              v-model="isFamili"
              v-bind:class=" isRedPswd ? 'red__outline' : ''"
              name="name"
              placeholder="Ваше Фамилия"/>
              <input
                class="form-control mb-2"
                style="display:inline;width:auto;"
                type="text" id="adress"
                v-model="isAdress"
                v-bind:class=" isRedPswd ? 'red__outline' : ''"
                name="name"
                placeholder="Ваш Адрес"/>
                <input
                  class="form-control mb-2"
                  style="display:inline;width:auto;"
                  type="password" id="password1"
                  v-model="isPassword"
                  v-bind:class=" isRedPswd ? 'red__outline' : ''"
                  name="password"
                  placeholder="Ваш Пароль"/>

        </form>
        <div class="row ml-2">
          <button
          type="submit"
            v-if="sinUping"
            v-on:click="handleSignUp"
            class="btn btn-outline-indigo"
            id="quickstart-sign-up"
            name="signup"
            >
            Регистрация
          </button>
        </div>
      </modal-body>
      <modal-footer v-if="!isLogin">
         Нет аккаунта ?
        <button class="btn btn-outline-indigo" color="outline-indigo" v-on:click="registration">Регистрация</button>
      </modal-footer>
      <modal-footer v-if="isLogin">
        уже есть аккаунт ?
        <button class="btn btn-outline-indigo" color="outline-indigo" v-on:click="logined">Войти</button>
      </modal-footer>
    </modal>
    <div class="modalWarning" v-if="isWarning">
      <div class="col-lg-5 col-md-6 col-sm-6 card pt-3 pb-3 pl-3 pr-3 " style="margin: 0 auto;">
        <div class="card-img mb-4">
          <img src="../../assets/logo.png" alt="">
        </div>
        {{warningAnswer}}

        <a  class="btn btn-outline-danger" name="button" v-on:click="reload"> Вернуться на главную</a>
      </div>
    </div>
    <hr style="width: 70%; display:block; margin:0 auto;"/>

  </div>
</template>

<script>
import { Modal, ModalHeader, ModalTitle, ModalBody, ModalFooter, Btn, Form, Chip } from 'mdbvue';
import { required, minLength  } from 'vuelidate';
export default {
  name: 'navbar',
  components: {
    Modal,
    ModalHeader,
    ModalTitle,
    ModalBody,
    ModalFooter,
    Btn,
    Chip,
  },
  data() {
    return {
      warningAnswer: '',
      isWarning: false,
      modal: false,
      isLogin: false,
      sinUping: false,
      lang: '',
      SignIn: false,
      status: '',
      isUser: '',
      notifyStatus: '',
      notifyVisible: false,
      isEmail: '',
      isPassword: '',
      isRed: true,
      isRedPswd: true,
      answer: '',
      answerPassword: '',
      answerName: '',
      isName: '',
      isFamili: '',
      answerFamili: '',
      isAdress: '',
      isPasswordDoble: '',
      answerDoble: '',
      answerReg: '',
      answerAlredy: '',
      isSinU: true,
      isLoginUser: ''

    }
  },
  mounted () {
    var getUser = localStorage.getItem('Logined')

    console.log(firebase.auth().currentUser)
    if (localStorage.getItem('Logined') === 'not log') {
      this.status = 'Вы не вошли, используйте  email для входа или зарегистрируйтесь'
      this.isLoginUser = 'Log In'

    } else if (localStorage.getItem('Logined') == null){
      this.status = 'Вы не вошли, используйте  email для входа или зарегистрируйтесь'
      this.isLoginUser = 'Log In'

    } else {
      this.status = 'Вы вошли как';
      this.isUser = getUser;
      this.isEmail = localStorage.getItem('Logined')
      this.SignIn = true;
      this.isLoginUser = getUser;
    }

  },

  methods: {
    reload () {
      setTimeout(
         function() {
             location.reload();
        }, 600
        );

    },
    change (){
        this.$router.push({ name: 'HomeEn'})
    },
    toggleSignIn() {

            var user = firebase.auth().currentUser
            var email = document.getElementById('email').value;
            var password = document.getElementById('password').value;
            if (email.length < 4) {
              this.notifyStatus = 'Please enter an email address.'
              return;
            }
            if (password.length < 4) {
              this.notifyStatus = 'Please enter a password.';
              return;
            }
            // Sign in with email and pass.
            // [START authwithemail]
            firebase.auth().signInWithEmailAndPassword(email, password).catch(function(error) {
              // Handle Errors here.
              var errorCode = error.code;
              var errorMessage = error.message;
              // [START_EXCLUDE]
              if (errorCode === 'auth/wrong-password') {
                this.notifyStatus = 'Wrong password.';
              } else {
              this.notifyStatus = errorMessage
              }
              console.log(error);
              // [END_EXCLUDE]
            });
            // [END authwithemail]
            this.SignIn = !this.SignIn;
            this.status = 'Вы вошли как :'
            localStorage.setItem('Logined', email)
            localStorage.setItem('user', 'true')
            this.isUser = localStorage.getItem('Logined')
            setTimeout(
               function() {
                   location.reload();
              }, 600
              );





        },
        signOut () {
          firebase.auth().signOut();
          this.SignIn = !this.SignIn
          this.status = 'Вы не вошли , импользуйте email для входа или зарешистрируйтесь'
          this.isUser = ''
          localStorage.setItem('Logined', 'not log')
          localStorage.setItem('user', 'false')
          this.isWarning = !this.isWarning;
          this.warningAnswer = 'Вы вышли, некоторые функции теперь не доступны'




        },
      handleSignUp () {
        var email = this.isEmail;
        var password = this.isPassword;
        var firstName = this.isName;
        var lastName = this.isFamili;
        var uid = new Date();
        var adress = this.isAdress;
        var password = this.isPassword;

        var postData = {
          user: email,
          firstName: firstName,
          lastName: lastName,
          uid: uid,
          adress: adress,
          password: password,
        };
        var newPostKey = firebase.database().ref().child('user-posts').push().key;

        // Write the new post's data simultaneously in the posts list and the user's post list.
        var updates = {};
        updates['/USERS/' + newPostKey] = postData;

        var email = this.isEmail;
        var password = this.isPassword;
        var self = this;

        firebase.database().ref().update(updates)
        firebase.auth().createUserWithEmailAndPassword(email, password).catch(function(error) {
          // Handle Errors here.
          var errorCode = error.code;
          var errorMessage = error.message;
          // [START_EXCLUDE]
          if (errorCode == 'auth/weak-password') {
            alert('The password is too weak.');
          } else {
            console.log(errorMessage);
          console.log(errorMessage)


          }
          console.log(error);
          if (error){
            if (errorCode == 'auth/invalid-email')
            self.answerReg = 'Неверный формат email'

            self.isSinU = true;

          } else {
            self.answerReg = 'Регистрация прошла успешно'
            self.isSinU = false;

          }
          // [END_EXCLUDE]
        });
    },
    registration () {
      this.isLogin = !this.isLogin
      this.sinUping = !this.sinUping;
    },
    logined () {
      this.isLogin = !this.isLogin
      this.sinUping = !this.sinUping;

    },
  },
  watch: {
  // эта функция запускается при любом изменении вопроса
  isEmail: function (val) {
    var newValue = val;
    if (newValue.length < 6) {
      console.log('Error')
      this.answer = 'Email должен быть не короче 6 символов'
      this.isRed = true;
    } else {
      this.isRed = false;
      this.answer = ''
    }
  },
  isPassword: function (val) {
    var newPValue = val;
    var password = this.isPassword;

    if (newPValue.length < 6) {
      console.log('Error')
      this.answerPassword = 'Пароль должен быть не менее 6-ти символов'
      this.isRedPswd = true;
    } else {
      this.isRedPswd = false;
      this.answerPassword = ''
    }
},
  isName: function (val) {
    var newPValue = val;
    if (newPValue.length < 2) {
      console.log('Error')
      this.answerName = 'Слишком короткое имя'
      this.isRedPswd = true;
    } else {
      this.isRedPswd = false;
      this.answerName = ''
    }
  },
  isFamili: function (val) {
    var newPValue = val;
    if (newPValue.length < 2) {
      console.log('Error')
      this.answerFamili = 'Слишком короткая Фамилия'
      this.isRedPswd = true;
    } else {
      this.isRedPswd = false;
      this.answerFamili = ''
    }
  }

},

}
</script>

<style lang="css">
.list-link {
  list-style-type: none;
  justify-content: center;
  align-items: center;
}
.--container-link {
  padding-top: 15px;
  padding-bottom: 15px;
}
.--container-link a {
  color: #2c3e50;
}
.link-item {
  position: relative;
}
.link-item::before {
  content: '';
  width: 30px;
  height: 2px;
  position: absolute;
  display: block;
  top: 100%;
  background-color: #3f51b5;
  transition: all .6s ease;
}
.link-item:hover::before {
  width: 100%
}
.aqua-gradient {
    background: -webkit-linear-gradient(50deg,#2096ff,#05ffa3)!important;
    background: -o-linear-gradient(50deg,#2096ff,#05ffa3)!important;
    background: linear-gradient(40deg,#2096ff,#05ffa3)!important;
}
.chip.chip-lg {
    height: 42px;
    line-height: 52px;
    border-radius: 26px;
}
.warning_chip {
  position: absolute;
  width: 230px;
  right: 30px;
  top: 130px;
  z-index: 999;
  animation: ChangesTop 2s 1;
  transition-duration: .7s;

}
.modalWarning {
  position: fixed;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  left: 0;
  top: 0;
  padding-top: 130px;
  z-index: 7777;
}
.red__outline {
  border:solid 1px red !important;
}
.red__outline:focus {
  outline: none;
}
@-webkit-keyframes ChangesTop {
  0%   { opacity: 0; top: 0; }
  100% { opacity: 1; top: 130px; }
}
@-moz-keyframes ChangesTop {
  0%   { opacity: 0; top: 0; }
  100% { opacity: 1; top: 130px; }
}
@-o-keyframes ChangesTop {
  0%   { opacity: 0; }
  100% { opacity: 1; top: 130px; }
}
@keyframes ChangesTop {
  0%   { opacity: 0; top: 0; }
  100% { opacity: 1;  top: 130px;}
}
</style>



