            <td class="pl-2 pr-2 pt-2 pb-2" style="border: solid 1px #444;">Нестираемая «квитанция» при проведении каждой транзакции со счета фонда для обеспечения полной прозрачности</td>
          </tr>
          <tr style="border: solid 1px #444;">
            <td class=" font-weight-bold pl-2 pr-2 pt-2 pb-2" style="border: solid 1px #444;">
