            </td>
            <td class="pl-2 pr-2 pt-2 pb-2" style="border: solid 1px #444;">
              Полностью соответствие установленным правилам, нормам,
              политике KYC и AML
            </td>
          </tr>
          <tr style="border: solid 1px #444;">
            <td class=" font-weight-bold pl-2 pr-2 pt-2 pb-2" style="border: solid 1px #444;">
              Кто финансируется
              из фонда?
            </td>
            <td class="pl-2 pr-2 pt-2 pb-2" style="border: solid 1px #444;">
              -Оказание помощи (гранты) некоммерческим экологическим
                проектам <br />
              - Маржинальное финансирование экологических проектов <br />
              - Финансирование геопортала, основанного на базе данных
