<script>
export default {
  data () {
    return {
      isSignUp: false,
    }
  },
  mounted () {
    if (localStorage.getItem('Logined') == 'not log') {
      this.isSignUp = true;
    } else if (localStorage.getItem('Logined') == null){
      this.isSignUp = true;
    } else {
      this.isSignUp = false;

    }
  },
}
</script>

<style lang="css">
#investFondSingle {

  padding-bottom: 60px;
}
</style>



