<template lang="html">
  <div id="investorsS">
    <p class="lead  investors-text col-lg-6 col-sm-8 col-10">
      EcoStart Platform - Your access to projects, the right investments and cooperation. The mission of the company is the right communication in the business community.
    </p>
    <div class="row pl-4">
      <button
        v-on:click="setproject"
        v-bind:class="isSignUp ? 'disabled' : ''"
        type="button"
        class="btn btn-outline-indigo mb-2"
        name="button">
          Place an investment offer
      </button>
    </div>
    <div class="row mb-5 pl-4">
      <span class="red-text" v-if="isSignUp">To add an investment offer you need to register</span>
    </div>

    <p class="lead ml-3 black-text"><strong>Benefits</strong></p>
    <ul style="list-style-type: none;">

      <li> Access to contacts of all project initiators </li>
            <li>Create a cryptograph </li>
            <li>Access to detailed information about the project <li />
            <li>Informing about new projects </li>
            <li>Individual search and selection of projects </li>
            <li>Verification of the authors of the project </li>
            <li>Getting the real rating of the project </li>
            <li>Attending Presentation Sessions organized by the EcoStart Platform </li>
    </ul>

  </div>
</template>

<script>

export default {
  name: 'Investors',
  data () {
    return {
      isSignUp: false,
    }
  },
  mounted () {
    if (localStorage.getItem('Logined') == 'not log') {
      console.log('not log ')
      this.isSignUp = true;
    } else if (localStorage.getItem('Logined') == null){
      console.log('null ')
      this.isSignUp = true;
    } else {
      console.log('true')
      this.isSignUp = false;

    }
  },
  methods: {
    setproject (){
        this.$router.push({ name: 'SetIniciatorsEn'})
    },
  },

}
</script>

<style lang="css">
.investors-text {
  max-width: 60%;
  margin-top: 40px;
}
#investorsS {
  padding-bottom: 60px;
}
</style>



