          <a class="fa-lg li-ic px-2"><fa icon="linkedin" /></a>
          <a class="fa-lg tw-ic px-2"><fa icon="twitter" /></a>
          <a class="fa-lg fb-ic px-2"><fa icon="facebook" /></a>
        </card-body>
      </card>
    </column>

    <column sm="4">
      <card wide>
        <view-wrapper hover cascade>
          <card-img src="https://mdbootstrap.com/img/Photos/Horizontal/Nature/4-col/img%20%28131%29.jpg" alt="Card image cap"></card-img>
          <md-mask flex-center waves overlay="white-slight"></md-mask>
