    <button  v-on:click="SetIniciators" v-bind:class="isSignUp ? 'disabled' : ''" type="button" class="btn btn-indigo" name="button">{{ setButton }}</button>
  </row>
  <row class="pt-1 justify-content-center mb-5">
    <span class="red-text" v-if="isSignUp">Чтобы добавить инвестиционное предложение необходимо зарегистрироваться</span>

  </row>

</div>
</template>

<script>
import Title from './Title'
import { Dropdown, DropdownItem, DropdownToggle,DropdownMenu } from 'mdbvue'
import { Container, Row, Column, Card, CardImg, CardHeader, CardBody, CardTitle, CardText, CardFooter, CardUp, CardAvatar, CardGroup, CardDeck, Btn, ViewWrapper, MdMask, Fa } from 'mdbvue';


export default {
  name: 'InvestorSingle',
  components: {
    Title,
    Dropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
    Container,
    Row,
    Column,
    Card,
    CardImg,
    CardHeader,
    CardBody,
    CardTitle,
    CardText,
    CardFooter,
    CardUp,
    CardAvatar,
    CardGroup,
    CardDeck,
    Btn,
    ViewWrapper,
    MdMask,
    Fa
  },
  data () {
    return {
      volumeInvest: 'Объем инвестиций',
      nameNumber: 'Название или номер инвестиционного предложения',
      datePost: 'По дате  публикации',
      coins: 'По сумме',
      topCoin: 'По возрастанию',
      downCoin: 'По убыванию',
      viewButton: 'Показать все',
      setButton:'Добавить предложение',
      isDisabled: true,
      isSignUp: false,
    }
  },
  mounted () {
    if (localStorage.getItem('Logined') == 'not log') {
      this.isSignUp = true;
    } else if (localStorage.getItem('Logined') == null){
      this.isSignUp = true;
    } else {
      this.isSignUp = false;

    }
  },
  methods: {
    SetIniciators () {
        this.$router.push({ name: 'SetIniciators'})
    }
  },
}
</script>

<style lang="css">
#investorsSingle {
  background: url('../../assets/investor.jpg');
  background-size: 100%;
  background-repeat: no-repeat;
}
</style>



