            <li>Имя: {{data_firstName}}</li>
            <li>Отчество: {{data_lastName}}</li>
            <li>Страна: {{selected_country}}</li>
            <li>Место учебы / работы {{data_placeJobLearning}}</li>
            <li>Ссылка на профиль в социальных сетях: {{data_socialProfile}}</li>
            <li>Email: {{data_email}}</li>

          </ul>
</div>
      </div>


