        { text: 'Ливан' },{ text: 'Ливия' },{ text: 'Литва' },{ text: 'Лихтенштейн' },{ text: 'Люксембург' },
        { text: 'Маврикий' },{ text: 'Мавритания' },{ text: 'Мадагаскар' },{ text: 'Македония' }, {text:'Малави'},
