<template lang="html">
  <section id="investFondSingle">
  <div class="row justify-content-center">
    <div class="col-lg-6">
      <img style="margin: 0 auto; margin-top: 130px;" class="mw-100" src="../../assets/investForest.png" alt="">
      <div class="row justify-content-center" style="width: 100%;">
        <button
          v-bind:class="isSignUp ? 'disabled' : ''"
          type="button"
          name="button"
          class="btn btn-outline-indigo mt-5 center mb-5">Smart Contract</button>

      </div>
      <span v-if="isSignUp" class="red-text">Smart Contract available only after registration</span>
    </div>
    <div class="col-lg-6">
      <h2 class="text-center lead font-weight-bold">IEF Features</h2>
      <table style="border: solid 1px #444;">
        <tbody style="border: solid 1px #444;">
          <tr style="border: solid 1px #444;">
            <td class="font-weight-bold pl-2 pr-2 pt-2 pb-2" style="border: solid 1px #444;">Blockchain technology</td>
            <td class="pl-2 pr-2 pt-2 pb-2" style="border: solid 1px #444;">An indelible "receipt" for each transaction from a fund account to ensure full transparency</td>
          </tr>
          <tr style="border: solid 1px #444;">
            <td class=" font-weight-bold pl-2 pr-2 pt-2 pb-2" style="border: solid 1px #444;">
              Compliance with legislation
            </td>
            <td class="pl-2 pr-2 pt-2 pb-2" style="border: solid 1px #444;">
              Fully compliant with established rules, regulations, KYC and AML policies
            </td>
          </tr>
          <tr style="border: solid 1px #444;">
            <td class=" font-weight-bold pl-2 pr-2 pt-2 pb-2" style="border: solid 1px #444;">
              Who is funded from the fund?
            </td>
            <td class = "pl-2 pr-2 pt-2 pb-2" style = "border: solid 1px # 444;">
              -Providing assistance (grants) to non-profit environmental
              projects <br />
            - Marginal financing of environmental projects <br />
            - Financing a database-based geoportal
              aerospace monitoring <br/>
            - Volunteer support
             </td>
          </tr>
          <tr style="border: solid 1px #444;">
            <td class=" font-weight-bold pl-2 pr-2 pt-2 pb-2" style="border: solid 1px #444;">
Selection of projects for financing
            </td>
            <td class="pl-2 pr-2 pt-2 pb-2" style="border: solid 1px #444;">
              The selection of environmental projects for funding is made:
               - by a majority vote of the owners of the TerraEcocoin coins,
                after being examined by EcoStart experts using e-voting technology
               - decentralized decision-making, for managing the funds of the fund.
            </td>
          </tr>
          <tr style="border: solid 1px #444;">
            <td class=" font-weight-bold pl-2 pr-2 pt-2 pb-2" style="border: solid 1px #444;">
              Escrow
            </td>
            <td class="pl-2 pr-2 pt-2 pb-2" style="border: solid 1px #444;">
              An escrow agent evaluates the voting and evaluation of the expertise and verifies the corresponding smart contract.
            </td>
          </tr>
          <tr style="border: solid 1px #444;">
            <td class=" font-weight-bold pl-2 pr-2 pt-2 pb-2" style="border: solid 1px #444;">
              Project monitoring
            </td>
            <td class="pl-2 pr-2 pt-2 pb-2" style="border: solid 1px #444;">
              The projects selected for financing will receive funds in stages, undergo a desk audit and report on their work. If the audit shows unsatisfactory results, funding may be suspended. Further funding may be provided as the project reaches its intended goals.
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
  </section>
</template>

<script>
export default {
  data () {
    return {
      isSignUp: false,
    }
  },
  mounted () {
    if (localStorage.getItem('Logined') == 'not log') {
      this.isSignUp = true;
    } else if (localStorage.getItem('Logined') == null){
      this.isSignUp = true;
    } else {
      this.isSignUp = false;

    }
  },
}
</script>

<style lang="css">
#investFondSingle {

  padding-bottom: 60px;
}
</style>



