<template lang="html">
  <div id="investorsS">
    <p class="lead  investors-text col-lg-6 col-sm-8 col-10">
      Платформа EcoStart – Ваш доступ к
      проектам, правильным инвестициям и
      сотрудничеству.
      Миссия компании –
      правильные коммуникации в
      бизнес-сообществе.
    </p>
    <div class="row pl-4">
      <button
        v-on:click="setproject"
        v-bind:class="isSignUp ? 'disabled' : ''"
        type="button"
        class="btn btn-outline-indigo mb-2"
        name="button">
          Разместить инвестиционное предложение
      </button>
    </div>
    <div class="row mb-5 pl-4">
      <span class="red-text" v-if="isSignUp">Чтобы добавить инвестиционное предложение необходимо зарегистрироваться</span>
    </div>

    <p class="lead ml-3 black-text"><strong>Преимущества</strong></p>
    <ul style="list-style-type: none;">
      <li>Доступ к контактам всех инициаторов проектов<li/>
      <li>Создание криптокошелька<li/>
      <li>Доступ к подробной информации о проекте<li/>
      <li>Информирование о новых проектах<li/>
      <li>Индивидуальный поиск и подбор проектов<li/>
      <li>Проверка благонадежности авторов проекта<li/>
      <li>Получение реального рейтинга проекта<li/>
      <li>Посещение Презентационных сессий, организованных Платформой EcoStart<li/>
    </ul>

  </div>
</template>

<script>

export default {
  name: 'Investors',
  data () {
    return {
      isSignUp: false,
    }
  },
  mounted () {
    if (localStorage.getItem('Logined') == 'not log') {
      console.log('not log ')
      this.isSignUp = true;
    } else if (localStorage.getItem('Logined') == null){
      console.log('null ')
      this.isSignUp = true;
    } else {
      console.log('true')
      this.isSignUp = false;

    }
  },
  methods: {
    setproject (){
        this.$router.push({ name: 'SetIniciators'})
    },
  },

}
</script>

<style lang="css">
.investors-text {
  max-width: 60%;
  margin-top: 40px;
}
#investorsS {
  padding-bottom: 60px;
}
</style>



