<template lang="html">
  <div class="">
    <div class="row justify-content-center align-items-center mt-3" >
      <a
        class="row justify-content-center align-items-center black-text"
        href="#">
        <i class="fab fa-youtube fa-3x mr-3"></i>О проекте
      </a>
    </div>
    <div class="row align-items-center justify-content-center">
      <div class="col-md-5">
        <p class="lead">
          Для пользования сервисом, необходимо
          установить встроенный в браузер
          (Google Chrome, Firefox или Opera)
          и онлайн-кошелек Metamask, в котором
          будут храниться токены (в дальнейшем
          Metamask будет привязан к странице
          регистрации проекта);
        </p>
      </div>
      <div class="col-md-5">
        <div class="card-img">
          <img class="terra-img mw-100" src="../../assets/terra.png" alt="">
        </div>
      </div>
    </div>
    <div class="offset-md-1 col-md-6">
      <span class="black-text lead font-weight-bold">Инструкция для пользователей </span>

      На текущий момент главная страница платформы,
      состоит из двух разделов: личный кабинет и
      Marketplace. На Marketplace размещены экологические
      проекты для инвестирования, список инвесторов и
      список волонтёров. 
    </div>

    <div class="col-md-5 mx-auto pt-5 pb-5">
      <h2 class="text-center">Инструкция  для владельцев монет TER</h2>
    </div>

    <div class="row align-items-center">
      <div class="offset-md-1 col-md-4 ">
        <p>
          1. При перечислении токенов
          на кошелёк другого лица необходимо
          указать адрес смарт контракта и адрес
          кошелька лица, которому направляются
          токены TER. Адрес смарт контракта для
          перевода средств:
        </p>
        <a class="mw-100 black-text lead font-weight-bold mb-3" href="https://etherscan.io/address/0x4a99256ac634C9307a3D7b6EABaFdEe1B2395C6e">https://etherscan.io/address/<br/>0x4a99256ac634C9307a3D7b6EABaFdEe1B2395C6e</a>

        <div class="m-100 mb-3"></div>
        <p>
          2. Смарт контракт автоматически
          направляет токены TER конечному
          адресату с расщеплением комиссии
          за транзакцию: одна часть для сети
          Etereum, другая часть направляется
          в кошелёк инвестиционного фонда (IEF)
          <span class="black-text lead font-weight-bold">0x1a2fdA26905a4428865fC772F8F2c92d59DEF3BF</span>
        </p>

      </div>

      <div class="col-md-6">
        <div class="card-img">
          <img class="mw-100" src="../../assets/schema.png" alt="">
        </div>
      </div>
    </div>

    <div class="col-md-5 mx-auto pt-5 pb-5">
      <h2 class="text-center">Инструкция  для Инвесторов</h2>
    </div>

    <div class="row align-items-center">
      <div class="col-md-6 offset-md-1">
        <div class="card-img">
          <img class="mw-100" src="../../assets/schema-investors.png" alt="">
        </div>
      </div>
      <div class="col-md-4">
        <p class="lead">
          1.Для поиска проектов следуйте логике сервиса на страничке сайта.
        </p>
        <p class="lead">
          2. Выбор проекта из списка на этом этапе проекта производится инвестором самостоятельно.
        </p>
        <p class="lead">
          3. При перечислении монет в кошелёк проекта инвестор самостоятельно оценивает проект.
        </p>
        <p class="lead">
          4. Инвестор может внести свой вклад в инвестиционный фонд. Взамен получит токены проектов, которые будут инвестироваться  фондом.
        </p>
      </div>
    </div>

    <div class="col-md-5 mx-auto pt-5 pb-5">
      <h2 class="text-center">Инструкция  для инициаторов проектов:</h2>
    </div>

    <div class="col-md-10 offset-md-1 mb-5">
      <p class="lead">
        EcoStart разрабатывает децентрализованную
        платформу c сервисом  по созданию именных
        цифровых токенов -  Token Generation Event (TGE).
        Любой инициатор проекта с использованием конструктора
        смарт контрактов сможет создать смарт контракт с
        заданными параметрами для инвестиций, эмитировать
        токены и начать продажу токенов, которые могут быть:
        продуктовыми, крипто векселями, долей в проекте,
        сертификатом участия, депозитным сертификатом, акцией,
        облигацией, криптовалютой, баллами программ лояльности и
        другими активами. В настоящей модели создание смарт
        контракта тоже
        самостоятельное, но ограничивается созданием смарт
        контракта и выпуском токенов по протоколу ERC20
      </p>
    </div>

    <div class="row justify-content-center">
      <div class="col-md-4">
        <div class=" col-md-8 card-img">
          <img class="mw-100" src="../../assets/img1.jpg" alt="">
        </div>
        <p>
          1. В качестве создателя контракта
          вы заходите на платформу, где вам
          предлагается авторизация, затем вы
          попадаете на главную страницу платформы.
        </p>
      </div>
      <div class="col-md-3">
        <div class=" col-md-8 card-img">
          <img class="mw-100" src="../../assets/img2.jpg" alt="">
        </div>
        <p>
          Для создания проекта,
          необходимо зайти в раздел
          "Projects" и далее в раздел,
          где отображаются созданные проекты,
          и можете добавить новый проект с помощью кнопки
        </p>
        <button v-on:click="setproject" class="btn btn-outline-indigo">ADD NEW</button>
      </div>
      <div class="col-md-3">
        <div class=" col-md-8 card-img">
          <img class="mw-100" src="../../assets/img3.png" alt="">
        </div>
        <p>
          Для создания смарт-контракта и выпуска токенов,
          Вы нажимаете кнопку "Create smart-contract"
          и переходите на сервис. Удачи !
        </p>
        <a class="btn btn-outline-indigo" href="https://icoconstructor.com/">Create smart-contract</a>
      </div>
    </div>

    <div class="row justify-content-center" style="width:100%">
      <div class="card mr-2 mt-5 mb-3  ml-2 card__category col-lg-4 col-sm-4 col-12" id="projects">
        <h2 class="title__before"><a class="white-text linkScroll" href="#projectsS">Проекты</a></h2>
      </div>
      <div class="card mr-2 mt-5 mb-3  ml-2 card__category col-lg-4 col-sm-4 col-12" id="infond">
        <h2 class="title__before"><a class="white-text linkScroll" href="#investFondSingle">Инвестиционный фонд</a></h2>

      </div>
      <div class="card mr-2 ml-2 mb-3 card__category col-lg-4 col-sm-4 col-12" id="investors">
        <h2 class="title__before"><a class="white-text linkScroll" href="#investorsSingle">Инвесторы</a></h2>

      </div>
      <div class="card mr-2 ml-2 mb-3 card__category col-lg-4 col-sm-4 col-12" id="volonters">
        <h2 class="title__before"><a class="white-text linkScroll" href="#volonteersSingle">Волонтеры</a></h2>

      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Head',
  data () {
    return {

    }
  },
  methods: {
    setproject (){
        this.$router.push({ name: 'SetProjects'})
    },
  },
}
</script>

<style lang="css">
.mw-100 { max-width: 100%;}
.terra-img{ width: 400px;}
.card__category {
  height: 250px;
  background: #000;
  position: relative;
}
.card__category img {
  max-width: 100%;
}
.card__category::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(63, 81, 181, .7);
  z-index: 333;
}
#projects {
  background: url('../../assets/projects.jpg');
  background-size: cover;
}
#infond {
  background: url('../../assets/investForest.png');
  background-size: cover;
}
#investors {
  background: url('../../assets/investor.jpg');
  background-size: cover;
}
#volonters {
  background: url('../../assets/volonter.jpg');
  background-size: cover;
}
.title__before {
  z-index: 444;
  text-align: center;
  color: #fff;
  margin-top: 30%;
  font-weight: bold;
  position: relative;
}
.title__before::before {
  content: '';
  width: 0;
  height: 2px;
  position: absolute;
  display: block;
  top: 40px;
  background-color: #fff;
  transition: all .6s ease;
  left: 0;
}
.title__before:hover::before {
  width: 100%
}
</style>



