import Vue from 'vue'
import Router from 'vue-router'
import HomeRu from '@/components/ru/HomeRu'
import HomeEn from '@/components/en/HomeEn'
import SetProjects from '@/components/SetProjects'
import SetProjectsEn from '@/components/SetProjectsEn'
import SetIniciators from '@/components/SetIniciators'
import SetIniciatorsEn from '@/components/SetIniciatorsEn'
import SetVolonteers from '@/components/SetVolonteers'
import SetVolonteersEn from '@/components/SetVolonteersEn'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HomeRu',
      component: HomeRu
    },
    {
      path: '/en',
      name: 'HomeEn',
      component: HomeEn,
      beforeEnter: (to, from, next) => {
         next()
      }
    },
    {
      path: '/setproject',
      name: 'SetProjects',
      component: SetProjects,
      beforeEnter: (to, from, next) => {
         next()
      }
    },
    {
      path: '/setprojecten',
      name: 'SetProjectsEn',
      component: SetProjectsEn,
      beforeEnter: (to, from, next) => {
         next()
      }
    },
    {
      path: '/setiniciator',
      name: 'SetIniciators',
      component: SetIniciators,
      beforeEnter: (to, from, next) => {
         next()
      }
    },
    {
      path: '/setiniciatoren',
      name: 'SetIniciatorsEn',
      component: SetIniciatorsEn,
      beforeEnter: (to, from, next) => {
         next()
      }
    },
    {
      path: '/setvolonters',
      name: 'SetVolonteers',
      component: SetVolonteers,
      beforeEnter: (to, from, next) => {
         next()
      }
    },
    {
      path: '/setvolontersen',
      name: 'SetVolonteersEn',
      component: SetVolonteersEn,
      beforeEnter: (to, from, next) => {
         next()
      }
    },



    // {
    //   path: '/chat',
    //   name: 'Chat',
    //   component: Chat,
    //   props: true,
    //   beforeEnter: (to, from, next) => {
    //     if (to.params.name) {
    //       next()
    //     } else {
    //       next({ name: 'Welcome'})
    //     }
    //   }
    // }
  ]
})



